const filterOptions: TransactionsFilterOpt[] = [
  { label: CategoriesTypesMap.INCOME, value: 'INCOME' },
  { label: CategoriesTypesMap.TRANSFER, value: 'TRANSFER' },
  { label: CategoriesTypesMap.EXPENSE, value: 'EXPENSE' },
];

export const useTransactionsActions = () => {
  const modal = useModalProvider();
  const { editById, create, deleteById } = useTransactionsService();
  const table = useTable();
  console.log('useTransactionsActions', table);

  return {
    top: [
      {
        name: 'editTr',
        title: ' ',
        iconId: iconId.edit,
        onClick: () => {
          modal.handleOpenModal({
            ModalChildren: TransactionForm,
            modalChildrenProps: { title: ' ', filterOptions, onSubmit: editById },
          });
        },
        disableChek: () => false,
      },
      {
        name: 'copyTr',
        title: ' ',
        iconId: iconId.copy,
        onClick: () => {
          modal.handleOpenModal({
            ModalChildren: TransactionForm,
            modalChildrenProps: { title: ' ', filterOptions, onSubmit: create },
          });
        },
        disableChek: () => false,
      },
      {
        name: 'deleteTr',
        title: ' ',
        iconId: iconId.delete,
        iconSize: '90%',
        onClick: (id: string) => {
          deleteById(id);
        },
        disableChek: () => true,
      },
    ],
    bottom: [
      {
        name: 'createIncomeTr',
        title: '',
        iconId: iconId.INCOME,
        iconSize: '90%',
        onClick: () => {
          modal.handleOpenModal({
            ModalChildren: TransactionForm,
            modalChildrenProps: {
              title: '',
              filterOptions,
              defaultState: { type: 'INCOME' },
              onSubmit: create,
            },
          });
        },
        disableChek: () => false,
      },
      {
        name: 'createTransferTr',
        title: '  ',
        iconId: iconId.TRANSFER,
        iconSize: '90%',
        onClick: () => {
          modal.handleOpenModal({
            ModalChildren: TransactionForm,
            modalChildrenProps: {
              title: '  ',
              filterOptions,
              defaultState: { type: 'TRANSFER' },
              onSubmit: create,
            },
          });
        },
        disableChek: () => false,
      },
      {
        name: 'createExpenseTr',
        title: '',
        iconId: iconId.EXPENSE,
        iconSize: '90%',
        onClick: () => {
          modal.handleOpenModal({
            ModalChildren: TransactionForm,
            modalChildrenProps: {
              title: '',
              filterOptions,
              defaultState: { type: 'EXPENSE' },
              onSubmit: create,
            },
          });
        },
        disableChek: () => false,
      },
    ],
  };
};


